import { ApolloError, FetchResult, Observable } from '@apollo/client/core';
import { GraphQLError } from 'graphql';
import objectHash from 'object-hash';
import { Ref, computed, onBeforeUnmount, ref, watch } from 'vue';
import { Subscription } from 'zen-observable-ts';
import { debugLog } from '../logger';

const subscriptionCache = new Map<string, Subscription>();

export const useSubscription = <T>({
  subscriptionKey,
  subscriptionFn,
  enabled,
}: {
  subscriptionKey: Ref<string>;
  subscriptionFn: () => Observable<FetchResult<T>>;
  enabled?: Ref<boolean>;
}) => {
  // define the reactive states
  const error = ref<readonly GraphQLError[] | ApolloError>();
  const data = ref<T | null | undefined>();

  const resetStates = () => {
    error.value = undefined;
    data.value = undefined;
  };

  const hash = computed(() => objectHash({ enabled: enabled?.value, subscriptionKey: subscriptionKey.value }));

  const observable = computed(() =>
    // create a new subscription if the key changes
    hash.value && (!enabled || enabled.value) ? subscriptionFn() : undefined
  );

  const subscription = computed(() => {
    if (subscriptionCache.has(hash.value)) return subscriptionCache.get(hash.value);
    const sub = observable.value?.subscribe({
      complete() {
        debugLog('subscription completed', subscriptionKey.value);
      },
      error(errorValue) {
        resetStates();
        error.value = errorValue;
      },
      next(value) {
        resetStates();
        const { data: resp, errors } = value;
        data.value = resp;
        if (errors) error.value = errors;
      },
    });
    if (sub) subscriptionCache.set(hash.value, sub);
    return sub;
  });

  // clear the subscription cache when the key changes
  watch(
    hash,
    (_curr, prev) => {
      if (prev) subscriptionCache.delete(prev);
    },
    { immediate: true }
  );

  watch(
    subscription,
    (_curr, prev) => {
      resetStates();
      // unsubscribe from the previous subscription automatically
      prev?.unsubscribe?.();
    },
    { immediate: true }
  );

  onBeforeUnmount(() => {
    // unsubscribe from the subscription when the component unmounts
    subscription.value?.unsubscribe?.();
    // clear the subscription cache
    subscriptionCache.delete(hash.value);
  });

  return {
    error,
    data,
    isSubscribing: computed(() => !subscription.value),
    isSubscribed: computed(() => !!subscription.value),
    isErrored: computed(() => !!error.value),
    isSuccess: computed(() => !!data.value),
  };
};
