import { apolloClient, handleApolloMutationResponse, handleApolloQueryResponse } from '@/utils/apollo';
import { useSubscription } from '@/utils/hooks/useSubscription';
import { ApolloQueryResult, FetchPolicy } from '@apollo/client/core';
import {
  clearCurrentReceiptMutation,
  createTransactionMutation,
  currentReceiptSubscription,
  getReceiptQuery,
  getReceiptTemplateQuery,
  linkReceiptToUserMutation,
  userReceiptsQuery,
} from '@pi-xcels/graphql';
import { MutationKeys, QueryKeys, SubscriptionKeys } from '@pi-xcels/graphql/constants';
import {
  CreateTransactionMutationVariables,
  CurrentReceiptSubscriptionVariables,
  GetReceiptQueryVariables,
  GetReceiptTemplateQuery,
  GetReceiptTemplateQueryVariables,
  LinkReceiptToUserMutationVariables,
  UserReceiptsQueryVariables,
} from '@pi-xcels/graphql/types';
import { sum } from '@pi-xcels/utils';
import { QueryFunctionContext, useInfiniteQuery, useMutation, useQuery } from '@tanstack/vue-query';
import objectHash from 'object-hash';
import { Ref, computed, readonly, ref } from 'vue';

export const useClearCurrentReceipt = () => {
  return useMutation({
    mutationKey: [MutationKeys.ClearCurrentReceipt],
    mutationFn: () =>
      apolloClient
        .mutate({
          mutation: clearCurrentReceiptMutation,
        })
        .then(handleApolloMutationResponse),
  });
};

export const useGetReceipt = (
  variables: Ref<GetReceiptQueryVariables>,
  options: { enabled: Ref<boolean> },
  // for invalidating the query
  customQueryKey: Readonly<Ref<Record<string, any>>> = readonly(ref({}))
) => {
  return useQuery({
    queryKey: [QueryKeys.GetReceipt, variables, customQueryKey],
    queryFn: () =>
      apolloClient
        .query({
          query: getReceiptQuery,
          variables: variables.value,
          fetchPolicy: 'no-cache',
        })
        .then(handleApolloQueryResponse),
    refetchOnWindowFocus: false,
    ...options,
  });
};

export const useGetReceiptTemplate = (
  variables: Ref<GetReceiptTemplateQueryVariables>,
  options: {
    enabled: Ref<boolean>;
    initialData?: ApolloQueryResult<GetReceiptTemplateQuery>['data'];
  }
) => {
  return useQuery({
    queryKey: [QueryKeys.GetReceiptTemplate, variables],
    queryFn: () =>
      apolloClient
        .query({
          query: getReceiptTemplateQuery,
          variables: variables.value,
          fetchPolicy: 'no-cache',
        })
        .then(handleApolloQueryResponse),
    refetchOnWindowFocus: false,
    ...options,
  });
};

export const useUserReceipt = (
  variables: Ref<Omit<UserReceiptsQueryVariables, 'offset'>>,
  tanStackQueryOptions?: { enabled: Ref<boolean> },
  apolloQueryOptions?: {
    nextFetchPolicy: FetchPolicy;
    fetchPolicy: FetchPolicy;
  }
) => {
  return useInfiniteQuery({
    queryKey: [QueryKeys.UserReceipts, variables as any],
    queryFn: ({ pageParam = { offset: 0 } }: QueryFunctionContext<string[], { offset: number }>) => {
      return apolloClient
        .query({
          query: userReceiptsQuery,
          variables: { ...variables.value, ...pageParam },
          ...apolloQueryOptions,
        })
        .then(handleApolloQueryResponse);
    },
    getNextPageParam: (lastPage, pages) => {
      if (!variables.value.limit) return;
      if (!lastPage?.userReceipts?.length) return;
      const hasNoMore = lastPage?.userReceipts?.length < variables.value.limit;
      if (hasNoMore) return;
      return {
        offset: sum(pages.map((page) => page.userReceipts?.length || 0)),
      };
    },
    refetchOnWindowFocus: false,
    ...tanStackQueryOptions,
  });
};

export const useCurrentReceiptSubscription = (
  variables: Ref<CurrentReceiptSubscriptionVariables>,
  options?: {
    enabled: Ref<boolean>;
  }
) => {
  return useSubscription({
    subscriptionKey: computed(() => objectHash([SubscriptionKeys.CurrentReceiptSubscription, variables.value.transactionRefs])),
    subscriptionFn: () =>
      apolloClient.subscribe({
        query: currentReceiptSubscription,
        variables: variables.value,
      }),
    ...options,
  });
};

export const useCreateTransaction = ({ retry }: { retry?: number }) => {
  return useMutation({
    mutationKey: [MutationKeys.CreateTransaction],
    mutationFn: ({ variables }: { variables: CreateTransactionMutationVariables }) =>
      apolloClient
        .mutate({
          mutation: createTransactionMutation,
          variables,
        })
        .then(handleApolloMutationResponse),
    retry,
  });
};

export const useLinkReceiptToUser = () => {
  return useMutation({
    mutationKey: [MutationKeys.LinkReceiptToUser],
    mutationFn: ({ variables }: { variables: LinkReceiptToUserMutationVariables }) =>
      apolloClient
        .mutate({
          mutation: linkReceiptToUserMutation,
          variables,
        })
        .then(handleApolloMutationResponse),
  });
};
